
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
XButton,
XInput,
Confirm,
Toast
},
data () {
return {
phone:'',
code:'',
time:0,
disabled: false,
btntxt:'获取认证码',
tips: '技巧讲解',
answer: '浏览实际情况请绕道而行。',
showcancel: true,
cancel: '重听',
congfirm:'关闭',
show: false,
params:{
'phone':'',
'type': 6
},
params02:{
'phone':'',
'code': ''
},
middle: 'middle',
msg:'请输入手机号',
showPositionValue: false
}
},
methods: {
timer: function () {
if (this.time > 0) {
this.time--;
this.btntxt = this.time + "s,后重新获取";
setTimeout(this.timer, 1000);
} else {
this.time = 0;
this.btntxt = "获取验证码";
this.disabled = false;
}
},
getCode(){
const that = this;
Drive.prototype.getCode(that.params).then(res => {
let list = res.data.data || [];
console.log(list)
if(res.data.status == '2000000'){

}else {
that.msg = res.data.message
that.showPositionValue = true
}
});
},
validateOldPhone(e) {
const that = this;
Drive.prototype.validateOldPhone(that.params02).then(res => {
console.log(res)
let list = res.data.data || {};
if(res.data.status == 2000000){
that.$router.push('../password')
}else{
that.poupfail(res.data.message)
}

that.show = true
});
},
isValid() {
return /\d{11}/.test(this.params.phone);
},
onSendCodeTap(e) {
const that = this;
if(this.isValid()){
if (that.params.phone) {
this.time=60;
this.disabled=true;
this.timer();
that.getCode()
} else {

that.showPositionValue = true
}
}

},
onComfirmTap (){

const that = this
this.params02.phone = this.params.phone
that.validateOldPhone()
},
onCancel () {
console.log('on cancel')
},
onConfirm (msg) {
console.log('on confirm')
if (msg) {
alert(msg)
}
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
poupsucess(msg){
this.success = true
this.tips='',
this.answer = '绑定手机号成功',
this.showcancel=false,
this.cancel='',
this.congfirm='确定',
this.show = true
},
poupfail(msg){
this.success = false
this.tips='',
this.answer = msg,
this.showcancel=false,
this.cancel='',
this.congfirm='确定',
this.show = true
}
}
}



